body {
  background: radial-gradient(circle, var(--color-dark-grey) 0%, var(--color-black) 100%);
}

.App {
  width: 100%;
  margin: 0 auto;
  font-family: 'Heebo', sans-serif;
}
.color {
  color: #868686
}

::-webkit-scrollbar {
  width: 12px;
  background-color: var(--color-dark-grey); /* Fondo del track */
}

::-webkit-scrollbar-thumb {
  background-color: var(--color-soft-black); /* Color del pulgar */
  border-radius: 10px;
  border: 3px solid var(--color-dark-grey); /* Bordes para un efecto de separación */
}

::-webkit-scrollbar-track {
  background: var(--color-black); /* Color de fondo del track */
  border-radius: 10px;
}

/* Hover en el pulgar */
::-webkit-scrollbar-thumb:hover {
  background: var(--color-light-grey); /* Color del pulgar al pasar el cursor */
}
